.intro_section{height:100vh;justify-content:center;background-color:#f2eeeb;color:#1d2a40;min-width:40rem}.introbox{margin:0 auto;text-align:center;max-width:50vw;height:100vh}.introbox_namebold{text-align:center;font-size:6em;font-weight:900;padding-top:10rem;line-height:1.1em;font-family:Source Sans Pro, sans-serif}.introbox_namelightleft{font-size:8rem;font-weight:900;transform:translate(-10rem);padding-top:10rem;line-height:1.1em;font-family:Source Sans Pro, sans-serif;font-style:italic}.introbox_namelightright{font-size:8rem;font-weight:900;transform:translate(10rem);padding-top:0rem;line-height:1.1em;font-family:Source Sans Pro, sans-serif;font-style:italic}.introbox_bio{font-size:1.5rem;font-weight:900;padding-top:2rem;line-height:1.1em;font-family:Source Sans Pro, sans-serif}.backgroundlines_bottom{position:absolute;background-size:100%;height:50%;width:50%;background-position:100% 0%;background-repeat:no-repeat;background-image:url(/my-app/static/media/lines.7227d841.png);bottom:0;right:0;z-index:1}.backgroundlines_bottom_header{font-size:10rem;left:0}.exp_section{height:100vh;justify-content:center;color:#1d2a40;margin-bottom:10rem;min-width:30rem;max-width:55rem;margin-left:auto;margin-right:auto}.exp_tile{background-color:black}.exp_tile_1{display:block;position:relative;width:100%;border-radius:20px;transition:all 0.3s ease-in-out;padding:3em 3em 4em;background-image:linear-gradient(#dbcdff, #a2c7dc);overflow-x:hidden;overflow-y:hidden}.exp_tile_1:hover{box-shadow:1px 1px 1em #b3b8ba}.exp_tile_1:hover img{transform:translateY(-10px)}.exp_tile_2{display:block;position:relative;width:100%;border-radius:20px;transition:all 0.3s ease-in-out;padding:3em 3em 4em;background-image:linear-gradient(#dbcdff, #a2c7dc);overflow-x:hidden;overflow-y:hidden;background-image:linear-gradient(to top left, #fbd5e8, #ffeec4)}.exp_tile_2:hover{box-shadow:1px 1px 1em #b3b8ba}.exp_tile_2:hover img{transform:translateY(-10px)}.exp_tile_category{text-align:right;display:block;font-size:14px;opacity:0.6;text-transform:uppercase;letter-spacing:1.5px;font-weight:700}.exp_tile_title{font-size:45px;text-align:right;display:block;margin-bottom:1em;letter-spacing:-1px}.exp_tile_holder{position:relative;max-width:100%;max-height:500px;margin:0 auto;text-align:center}.exp_tile_image{transition:all 0.3s ease-in-out;max-width:600px;max-height:500px;position:relative;border-radius:30px;display:inline-block;vertical-align:middle}body{margin:0;padding:0;width:100%;height:100%;box-sizing:border-box;font-family:"Source Sans Pro", sans-serif;background-color:#f2eeeb}*{box-sizing:border-box}

